import React, { useEffect } from "react";
import { Skeleton, Table, Card } from "antd";
import Style from "./style.module.css";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import { getBacklog } from "../../../action/project";
import useTitle from "../../util/useTitle";

const Index = props => {
  const columns = [
    {
      title: "name",
      dataIndex: "storyName",
      key: "name"
    },
    {
      title: "sprint",
      dataIndex: "sprint.sprintName",
      key: "sprint"
    },
    {
      title: "activity",
      dataIndex: "activity.activityName",
      key: "activity"
    },
    {
      title: "story point",
      dataIndex: "storyPoint",
      key: "storyPoint",
      sorter: (a, b) => a.storyPoint - b.storyPoint
    },
    {
      title: "status",
      dataIndex: "status",
      key: "status"
    },
    {
      title: "priority",
      dataIndex: "priority",
      key: "priority",
      filters: [
        { text: "must", value: "Must" },
        { text: "should", value: "Should" },
        { text: "could", value: "Could" }
      ],
      onFilter: (value, record) => record.priority.includes(value)
    },
    {
      title: "is epic",
      dataIndex: "isEpic",
      key: "epic",
      filters: [{ text: "false", value: false }, { text: "true", value: true }],
      render: value => <>{value ? "yes" : "no"}</>,
      onFilter: (value, record) => record.isEpic === value
    }
  ];

  const dispatch = useDispatch();
  const { loading, stories } = useSelector(
    state => state.projectStories,
    shallowEqual
  );

  useEffect(() => {
    dispatch(getBacklog(props.match.params.projectId));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props]);
  useTitle("project backlog");

  return (
    <Card>
      {/* {loading ? (
        <>
          <div className={`${Style.usBox} `}>
            <div
              style={{
                height: "120px",
                overflow: "hidden",
                width: "100%",
                margin: "auto 0"
              }}
            >
              <Skeleton active />
            </div>
          </div>
        </>
      ) : ( */}
      <Table
        rowKey={record => record.storyId}
        columns={columns}
        loading={loading || !stories?.length}
        expandedRowRender={record => (
          <div style={{ margin: 0, color: "red", fontWeight: "400" }}>
            {" "}
            <p>
              {" "}
              {"as:"} {record.as}
            </p>
            <p>
              {" "}
              {"i want to:"} {record.iWant}
            </p>{" "}
            <p>
              {" "}
              {"so that :"} {record.soThat}
            </p>
          </div>
        )}
        dataSource={stories}
        pagination={{ position: "top" }}
      // onChange={handleChange}
      />
      {/* )} */}
    </Card>
  );
};

export default Index;
